<template>
<transition name="slide-top-menu">
  <div class="slidemenu" v-show="visible">
    <div class="slidemenu-conent">
      <!-- <div class="login-header">
        <div class="top-back" @click="goBack">
          <span class="iconfont icon-return-arrow"></span>
        </div>
        <div class="center">
          登录

        </div>
      </div> -->
      <custHeader :title="title"></custHeader>
      <div class="header-op-con">  <router-link to="/">跳过</router-link></div>
    
      <div class="login-img">
        <i class="fa fa-user-circle-o"></i>
      </div>
      <div class="login-inputs">
        <mt-field label="登录方式" placeholder="请选择登录方式" type="bcustCode" v-model="custIdType" v-lfoucs="showAcctType" v-show=false>
         
        </mt-field>
        <div class=" input-icon-c">
          <mt-field label="" placeholder="手机号/客户号/资金号" type="bcustCode" v-model="bcustCode" v-validtype="rules.bcustCode" :state="rules.bcustCode.state">
         
        </mt-field>
         <i class="fa fa-user"  ></i>
        </div>
        <div  class=" input-icon-c">
        <mt-field label="" placeholder="请输入密码" :type="passwordType" v-model="password" >

        </mt-field>
         <i class="fa fa-lock"  ></i>
         <i class="eye-open fa" :class="faeye" @click="openEye"></i>
        </div>
        <div class="rember-con"><mt-checklist :amx="1" v-model="remberFlag" :options="remberFlagOptions"></mt-checklist></div>
        <div class="login-btns">
          <mt-button type="danger" size="large" @click="login" :disabled="valiSuccess">登录</mt-button>
          <div class="normal-box login-other" style="margin-top:6px; ">
            <div class="t-left">
              <a href="#findPassword">
                      忘记密码?
                  </a>
            </div>
            <div class="t-right">
              <router-link to="/register">
                快速注册
              </router-link>
            </div>
          </div>


        </div>
      </div>
    </div>

    <mt-popup v-model="acctTypeVisible" style="width:100%" position="bottom">
      <button class="btn" @click="dissAcctType">取消</button>
      <button class="btn" @click="sureAcctType">确认</button>
      <mt-picker :slots="acctTypeSlots" class="acctTypepicker" valueKey="name" @change="acctTypeChange" :visible-item-count="3"></mt-picker>
    </mt-popup>
  </div>
</transition>
</template>

<script>
import {
  Field,
  Button,
  MessageBox,
  Checklist
} from 'mint-ui';
import cookieUtil from 'js-cookie'
import custHeader from  'components/custHeader'
import src1 from "assets/l1.png"
import src2 from "assets/l2.png"
Vue.component(Button.name, Button);

Vue.component(Checklist.name, Checklist);
Vue.component(Field.name, Field);
export default {
  name: 'slidemenu',
  data() {
    return {
      passwordType:"password",
      faeye:"fa-eye-slash",
      remberFlag:["true"],
      remberFlagOptions:[{label:"记住账号",value:"true"}],
    smrz:src1,
            fxpc:src2,
        title:"登录",
      rules: {
        bcustCode: {
          str: "required",
          state: ""
        },
        password: {
          str: "required",
          state: ""
        },

      },
      visible: true,
      custIdType: "客户号",
      bcustCode: '',
      password: '',
      acctTypeSlots: [{
        flex: 2,
        values: [{
          value: '0',
          name: '客户号'
        }, {
          value: '1',
          name: '资金号'
        }, {
          value: '2',
          name: '磁卡号'
        }],
        className: "slots"
      }],
      acctTypeVisible: false,
    }
  },
  computed: {

  },
  created: function() {
        if(cookieUtil.get("remberFlag") && cookieUtil.get("remberFlag")=="true"){
          this.bcustCode=cookieUtil.get("remberBcustCode");
        }
  },
  components:{
      custHeader
  },
  watch: {
    // remberFlag(remberFlag){
    //   console.log(remberFlag);
    //   console.log(this.remberFlag[0]);
    // },
    value(val) {
      this.visible = val.isShow;
    },
    visible(val) {
      this.value.isShow = true;
    }
  },
  computed:{
    valiSuccess:function(){
      return (this.bcustCode!="" && this.bcustCode!="" )? false:true;
    },
  },
  methods: {
    openEye:function(){
      if(this.passwordType=="password"){
        this.passwordType="text";
        this.faeye="fa-eye";
      }else{
        this.passwordType="password";
         this.faeye="fa-eye-slash";
      }
    },
    acctTypeChange: function(picker, value) {
      console.log(value)
      if (value[0])
        this.custIdType = value[0].name
      // this.acctTypeValue = value[0].value
    },
    sureAcctType: function() {

      this.acctTypeVisible = false
    },
    dissAcctType: function() {
      this.acctTypeVisible = false
    },
    showAcctType: function() {
      this.acctTypeVisible = true
    },
    goBack: function() {
      this.$router.go(-2);
    },
    login: function() {
      var self = this;
      if (global.isOpenMd5) {
        this.password = global.md5(this.password);
      }
      axiosUtil.upCltLogin({
        custIdType: 0,
        custId: this.bcustCode,
        password: this.password
      }).then(function(data) {
        var data = data.data;
        if (data.errorcode == 1) {
          MessageBox("提示", data.errormsg);
        } else {
          var userData = data;
            cookieUtil.set("mb", userData.data.mobilePhone,{expires:1,path:global.webSite})
          cookieUtil.set("trdAcct", userData.data.trdAcct, {
            expires: 1,
            path: global.webSite
          })
          cookieUtil.set("userCode", userData.data.userCode, {
            expires: 1,
            path: global.webSite
          })
          if(self.remberFlag.length>0){
            cookieUtil.set("remberFlag", "true", {
            expires: 1,
            path: global.webSite
            })
             cookieUtil.set("remberBcustCode", self.bcustCode, {
            expires: 1,
            path: global.webSite
            })
          }else{
              cookieUtil.set("remberFlag", "false", {
            expires: 1,
            path: global.webSite
            })
               cookieUtil.set("remberBcustCode", "", {
            expires: 1,
            path: global.webSite
            })
          }
           
          if (userData.data.firstRegLogin) {
              //self.$router.push();//跳到登录密码修改
          } else {
            if (self.$route.query.referrer) {
              self.$router.push(self.$route.query.referrer);
            } else {
              self.$router.push("/");
            }
          }

          // axiosUtil.upCltStkTrdacctQry().then(function(data) {
          //   var data = data.data;
          //   var trdacct = data[0].trdacct; //0000160115
          //   cookieUtil.set("trdAcct", data[0].trdacct,{expires:1,path:global.webSite})
          //   cookieUtil.set("userCode", userData.data.userCode,{expires:1,path:global.webSite})
          //   console.log(self.$route.query);
          //   if(self.$route.query.referrer){
          //     self.$router.push(self.$route.query.referrer);
          //   }else{
          //     self.$router.go(-1);
          //   }

          // })

        }

      })
    }

  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.slidemenu {
  position: fixed;
  width: 100%;
  height: 100%;
  z-index: 1001;
  left: 0;
  top: 0;
  background-color: rgba(0, 0, 0, .3);
  transition: .5s all ease;
}

.slidemenu-conent {
  height: 100%;
  width: 100%;
  background: #fff;
}

.menu-list {
  color: #111;
}

.login-img {
  height: 200px;
      text-align: center;
    line-height: 280px;
}

.login-inputs {
  width: 90%;
  margin: 0 auto;
}

.login-btns {
  width: 90%;
  margin: 12px auto;
}

.login-ghost {
  background: rgba(0, 0, 0, 0);
  border: 1px solid #ef4f4f;
  color: #ef4f4f;
  margin-top: 12px;
}

.login-header {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  z-index: 3;
  height: 45px;
  line-height: 45px;
  font-size: 19px;
  background: #ef4f4f;
  color: #ffffff;
  text-align: center;
}

.top-back {
  max-width: 85px;
  float: left;
}

.btn {
  width: 49%;
  background-color: #fff;
  border: 0;
  height: 40px;
  font-size: 20px;
  outline: 0;
}
.login-img .fa-user-circle-o{
  font-size: 100px;
    color: rgba(0,0,0,.5);
}
.login-other a{
  text-decoration: none;
  color: #FF3C3C;
  font-size: 12px;
}
</style>



